import "./styles/contacts.css";
import React, { useState, useEffect } from 'react';
import { Page } from "../../../pages";
import { Helmet } from "react-helmet";
import { Breadcrumbs } from "../../../components";

const Contacts = () => {

    return(
        <Page>
            <Helmet>
                <title>{"E-SHOP | Контакти"}</title>
            </Helmet>
            <div class="conteiner">
                <Breadcrumbs />
                <div className="contacts">

                    <div className="info-block">
                    <h2 className="info-block__title">Контакти</h2>
                        <div className="info-block__content">

                            <div className="info-block__section">
                                <h3 className="info-block__subtitle">Гаряча лінія</h3>
                                
                                <p className="info-block__text">
                                    Телефон гарячої лінії, щодня з 08:00 до 19:00
                                </p>

                                <p className="info-block__text">
                                    <span className="info-block__text--bold">+38 (093) 333-33-33</span>
                                    <br />
                                    <span className="info-block__text--bold">+38 (096) 666-66-66</span>
                                    <br />
                                    <span className="info-block__text--bold">+38 (067) 777-77-77</span>
                                </p>

                                <p className="info-block__text">
                                    <span className="info-block__text--bold">E-mail: </span>e.shop@gmail.com
                                </p>

                            </div>

                            <div className="info-block__section">
                        
                                <p className="info-block__text">
                                    Якщо у вас є скарги, пропозиції або коментарі щодо роботи інтернет-магазину 
                                    E-SHOP - обов'язково повідомте нас про це по телефону, або залиште звернення 
                                    на електрону скриньку! Орієнтований час відповіді на письмові звернення - 3 робочих дні.
                                </p>

                            </div>

                        </div>
                    </div>

                </div>
            </div>
        </Page>
    );
};

Contacts.displayName = '/contacts';
export default Contacts;