import "./styles/about.css";
import React, { useState, useEffect } from 'react';
import { Page } from "../../../pages";
import { Helmet } from "react-helmet";
import { Breadcrumbs } from "../../../components";

const About = () => {

    return(
        <Page>
            <Helmet>
                <title>{"E-SHOP | Про компанію"}</title>
            </Helmet>
            <div class="conteiner">
                <Breadcrumbs />
                <div className="delivery">

                    <div className="info-block">
                    <h2 className="info-block__title">Про компанію</h2>
                        <div className="info-block__content">

                            <div className="info-block__section">
                                <h3 className="info-block__subtitle">Основна інформація</h3>
                                
                                <p className="info-block__text">
                                    <span className="info-block__text--bold">E-SHOP</span> - це інтернет-магазин мобільної техніки, створений для того, 
                                    щоб допомогти вам знайти найновіші гаджети та аксесуари за найкращими цінами. 
                                    Ми пропонуємо широкий вибір смартфонів від провідних світових брендів.
                                </p>

                                <p className="info-block__text">
                                    Важливою складовою стратегії розвитку компанії є прозора та ефективна діяльність. 
                                    Тому E-SHOP: з 2023 року проходить аудиторську перевірку у провідних компаній 
                                    "Великої четвірки", продає виключно офіційну та сертифіковану техніку від 
                                    провідних виробників та постачальників, відповідально ставиться до виконання зобов’язань 
                                    перед державою. 
                                </p>

                            </div>

                            <div className="info-block__section">
                                <h3 className="info-block__subtitle">Соціальна відповідальність</h3>
                                
                                <p className="info-block__text">
                                    <span className="info-block__text--bold">E-SHOP</span> — соціально відповідальна компанія. На тлі тривалої військової агресії 
                                    та повномасштабного вторгнення росії, один із пріоритетних напрямів соціальної 
                                    відповідальності компанії була та залишається допомога українському війську 
                                    та волонтерам. Також компанія підтримує екологічні ініціативи, спрямовані 
                                    на відповідальне споживання природних ресурсів та утилізацію відходів.
                                </p>
                            </div>

                        </div>
                    </div>

                </div>
            </div>
        </Page>
    );
};

About.displayName = '/about';
export default About;