import React from "react";
import { Page } from "../../../pages";
import "./styles/notFound.css";
import notFoundImage from "../../../../theme/img/icons/not-found.png";
import { Helmet } from "react-helmet";
import { Breadcrumbs } from "../../../components";

const NotFound = () => {
    return(
        <Page>
            <Helmet>
                <title>{"E-SHOP | Строрінка не знайдена"}</title>
            </Helmet>
            <div class="conteiner">
                <Breadcrumbs />
                <div className="not-found">

                    <div className="not-found__image">
                        <img src={notFoundImage} alt="404 - Сторінка не знайдена" />
                    </div>

                    <div className="not-found__info">
                        <p className="not-found__title">Упс, щось пішло не так...</p>
                        <p className="not-found__description">
                            Здається, сторінки за цим посиланням не існує.<br />
                            Перевірте правильність посилання!
                        </p>
                    </div>
                    
                </div>
            </div>
        </Page>
    )
}

export default NotFound;